import React from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup
} from "@vkontakte/vkui";

const ModalBuyTransfer = ({
  setActiveModal,
  openPaymentSystem,
  onChangeHandlerPayment,
  onChangeHandlerAmount,
  type,
  onChangeHandlerId,
  transfer

}) => {
  return (
    <>
      {type === "buy" ? (
        <ModalRoot activeModal="buy-durkacoins">
          <ModalCard
            id="buy-durkacoins"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
              />
            }
            header="–ö—É–ø–∏—Ç—å DurkaCoins"
            caption={
              <>
                <FormLayout>
                  <Input
                    top="–£–∫–∞–∂–∏—Ç–µ –∫–æ–ª-–≤–æ DuraCoins"
                    placeholder="5"
                    onChange={onChangeHandlerPayment}
                    type="number"
                  />
                </FormLayout>
                <InfoRow id="toPay">–ö –æ–ø–ª–∞—Ç–µ: 0 —Ä—É–±</InfoRow>
              </>
            }
            actions={[
              {
                title: "–ö—É–ø–∏—Ç—å",
                mode: "commerce",
                onChange: { onChangeHandlerAmount },

                action: () => {
                  openPaymentSystem();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : (
        <ModalRoot activeModal="transfer">
          <ModalCard
            id="transfer"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
              />
            }
            header="–ü–µ—Ä–µ–≤–µ—Å—Ç–∏ DurkaCoins"
            caption={
              <>
                <FormLayout>
                  <Input
                    top="–í–≤–µ–¥–∏—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ø–æ–ª—É—á–∞—Ç–µ–ª—è"
                    placeholder="https://vk.com/durkacoin"
                    onChange={onChangeHandlerId}
                    type="text"
                  />
                  <FormLayoutGroup top="–£–∫–∞–∂–∏—Ç–µ –∫–æ–ª-–≤–æ DurkaCoins">
                    <Input
                      onChange={onChangeHandlerAmount}
                      placeholder="10"
                      type="number"
                    />
                  </FormLayoutGroup>
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "–ü–µ—Ä–µ–≤–µ—Å—Ç–∏",
                mode: "commerce",
                action: () => {
                  transfer();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      )}
    </>
  );
};

export { ModalBuyTransfer };
