import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
} from "@vkontakte/vkui";

import {
  InventoryItem,
  MiningInventoryItem,
  ListUsers,
  ExchangeSell,
  ExchangeBuyItem,
  ModalExchangeBuy
} from "../components";

import { formatCoins, requestMethod } from "../helpers";

const Exchange = ({
  id,
  dataInventory,
  targetIdInventory,
  openUseItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  dataMiners,
  miners,
  durkaCoins,
  course,
  openAlertPay,
  setActiveModal,

}) => {
  const [costOneDC, setCostOneDC] = useState('');
  const [qiwiAccount, setQiwiAccount] = useState('+7');
  const [inputDurkaCoins, setInputDurkaCoins] = useState('');

  const [amountDurkaCoins, setAmountDurkaCoins] = useState('');

  const [activeTab, setActiveTab] = useState("buy");
  const [dataExchangeBuy, setDataExchangeBuy] = useState([]);
  let targetIdExchangeBuy = 0;
  
  function tabBuy() {
    setActiveTab("buy");
    window.scrollTo(0, 0);
  }

  function tabSell() {
    setActiveTab("sell");
    window.scrollTo(0, 0);
  }

  function getExchangeList() {
    axios.post(`${requestMethod}/getExchangeList`, {
      search: document.location.search
    }).then(({ data: { message } }) => {
      setDataExchangeBuy(message);
    }).catch((error) => {
      console.log('[EXCHANGE ERROR]', error);
    });
  }

  useEffect(() => {
    getExchangeList();
  }, []);


  function addExchangeOffer() {
    if (
      !inputDurkaCoins
      || !costOneDC
      || !qiwiAccount
    ) {
      return openAlertPay("–¢—ã –∑–∞–±—ã–ª –∑–∞–ø–æ–ª–Ω–∏—Ç—å –≤—Å–µ –ø–æ–ª—è", "red");
    }

    console.log({
      inputDurkaCoins,
      costOneDC,
      qiwiAccount
    })

    if (!qiwiAccount.startsWith('+7')) {
      // –ú–æ–¥–∞–ª–∫—É –æ —Ç–æ–º —á—Ç–æ –¥–æ–ª–∂–µ–Ω –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è –Ω–∞ +7
      return openAlertPay("–í–≤–µ–¥–∏ –Ω–æ—Ä–º–∞–ª—å–Ω—ã–π –Ω–æ–º–µ—Ä —à–∏–∑–æ–∏–¥", "red");
    }
    
    if (
      Number.isNaN(+inputDurkaCoins)
      || (+inputDurkaCoins ^ 0) !== +inputDurkaCoins
    ) {
      return openAlertPay("–ö–æ–ª-–≤–æ DurkaCoins –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å —Ü–µ–ª—ã–º —á–∏—Å–ª–æ–º, —Ç–µ–±—è –∫—Ç–æ –∏–∑ –ø–∞–ª–∞—Ç—ã –≤—ã–ø—É—Å—Ç–∏–ª?", "red");
    }

    if (costOneDC > course) {
      return openAlertPay("–°—Ç–æ–∏–º–æ—Å—Ç—å –æ–¥–Ω–æ–≥–æ DurkaCoin –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –Ω–∏–∂–µ —Ç–µ–∫—É—â–µ–≥–æ –∫—É—Ä—Å–∞", "red");
    }

    if (Number.isNaN(+costOneDC)) {
      return openAlertPay("–°—Ç–æ–∏–º–æ—Å—Ç—å –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —á–∏—Å–ª–æ–º, –∞–ª–ª–æ...", "red");
    }

    if (inputDurkaCoins * 100_000 > durkaCoins) {
      return openAlertPay("–£ —Ç–µ–±—è –Ω–µ —Ö–≤–∞—Ç–∞–µ—Ç –¥–µ–Ω—è–∫ –Ω–∞ –±–∞–ª–∞–Ω—Å–µ –ª–£–ª", "red");
    }

    axios.post(`${requestMethod}/createExchangeOffer`, {
      qiwiAccount,
      costOneDC,
      durkaCoins: inputDurkaCoins,
      search: document.location.search
    }).then(({ data: { status, message }}) => {
      console.log({ status, message });

      if (status === 'denied') {
        return openAlertPay(message, "red");
      }
      
      openAlertPay(message, 'green');
      getExchangeList();

      setCostOneDC('');
      setInputDurkaCoins('')
      setQiwiAccount('+7');
    }).catch((error) => {
      console.log('createExchangeOffer error', error);
    })
  }

  function onChangeAction(e, type, costOneDC, remainingDC) {
    const value = e.target.value.trim();
  
    switch (type) {
      case 'DC':
        setInputDurkaCoins(value);
        break;
      case 'costOneDC':
        setCostOneDC(value.replace(',', '.'));
        break;
      case 'qiwiAccount':
        setQiwiAccount(value);
        break;
      case 'modal':
        let text = `–ö –æ–ø–ª–∞—Ç–µ: ${(value * costOneDC).toFixed(2)} —Ä—É–±`
        if (Number.isNaN(+value)) {
          text = `–°—É–º–º–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —á–∏—Å–ª–æ–º`;
        }

        if (remainingDC / 100_000 < value) {
          text = `–ö–æ–ª-–≤–æ –Ω–µ –¥–æ–ª–∂–Ω–æ –ø—Ä–µ–≤—ã—à–∞—Ç—å –æ—Å—Ç–∞–≤—à–∏–µ—Å—è –Ω–∞ –ø—Ä–æ–¥–∞–∂—É DurkaCoins`
        }

        document.querySelector('#exchangeBuy').innerHTML = text;
        setAmountDurkaCoins(value);
        break;
    }
  }

  function openExchangeBuy(e) {
    targetIdExchangeBuy = e.currentTarget.id;
    const { costOneDC, remainingDC } = dataExchangeBuy[targetIdExchangeBuy - 1];

    setActiveModal(
      <ModalExchangeBuy
        targetIdExchangeBuy={targetIdExchangeBuy}
        setActiveModal={setActiveModal}
        costOneDC={costOneDC}
        remainingDC={remainingDC}
        onChangeAction={onChangeAction}
        amountDurkaCoins={amountDurkaCoins}
      />
    );
  }

  const renderExchangeBuy = dataExchangeBuy.map((item, index) => {
    return (
      <ExchangeBuyItem
        key={index}
        id={index + 1}
        idVk={item.userId}
        fullName={item.fullName}
        photoURL={item.photoURL}
        durkaCoins={item.durkaCoins}
        costOneDC={item.costOneDC}
        remainingDC={item.remainingDC}
        openExchangeBuy={openExchangeBuy}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={<PanelHeaderBack onClick={goHome} data-to="home" />}
      >
        –ë–∏—Ä–∂–∞
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "–í—ã –ø–æ–¥–∫–ª—é—á–∏–ª–∏—Å—å —Å 2 —É—Å—Ç—Ä–æ–π—Å—Ç–≤ ",
          socketReconnect,
          "–ü–µ—Ä–µ–ø–æ–¥–∫–ª—é—á–∏—Ç—å—Å—è"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
              <TabsItem onClick={tabBuy} selected={activeTab === "buy"}>
                –ö—É–ø–∏—Ç—å
              </TabsItem>
              <TabsItem onClick={tabSell} selected={activeTab === "sell"}>
                –ü—Ä–æ–¥–∞—Ç—å
              </TabsItem>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "–û—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∏–Ω—Ç–µ—Ä–Ω–µ—Ç - —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ!",
              updateConnection,
              "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "–û—à–∏–±–∫–∞ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è –∫ —Å–µ—Ä–≤–µ—Ä—É",
                  socketReconnect,
                  "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
                )
              ) : (
                <>
                  {activeTab === "buy" ? (
                    <>
                      {/* {renderInventory.length
                                                ? */}
                      <Div style={{ marginTop: 55 }}>
                        {renderExchangeBuy}
                      </Div>
                      {/* // : */}
                      {/* <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 90 }} />
                      </div> */}
                      {/* // } */}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "sell" ? (
                    <>
                      <Div
                        style={{
                          marginTop: 55,
                        }}
                      >
                        <ExchangeSell
                          durkaCoins={formatCoins(durkaCoins)}
                          course={course}
                          addExchangeOffer={addExchangeOffer}
                          onChangeAction={onChangeAction}
                          costOneDC={costOneDC}
                          inputDurkaCoins={inputDurkaCoins}
                          qiwiAccount={qiwiAccount}
                        />
                      </Div>
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}

              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Exchange };
