import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import { getRankByRating, formatCoins } from "../helpers";

const ExchangeBuyItem = ({
  id,
  fullName,
  durkaCoins,
  photoURL,
  costOneDC,
  idVk,
  remainingDC,
  openExchangeBuy,
}) => {
  const urlUser = "https://vk.com/id" + idVk;
  const sumRUB = durkaCoins / 100_000 * costOneDC;
  const remainingAmountRUB = remainingDC  / 100_000 * costOneDC;

  

  return (
    <Div className="card">
      <span className="listNumber">{id}</span>

      <Cell
        className="myItem"
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div style={{ padding: 0, marginLeft: 10 }}>
            <Div
              style={{
                padding: 2,
                margin: 0,
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
              <Div className="div" style={{ whiteSpace: "nowrap" }}>
                1
              </Div>
              <Div className="iconDC" style={{ marginLeft: 3 }} />
              <Div className="div" style={{ whiteSpace: "nowrap" }}>
                = {costOneDC} —Ä—É–±
              </Div>
            </Div>
            <Div
              style={{
                padding: 2,
                margin: 0,
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
              <Div>–í—Å–µ–≥–æ {durkaCoins / 100_000}</Div>
              <Div className="iconDC" style={{ marginLeft: 3 }} />
              <Div>({sumRUB} —Ä—É–±)</Div>
            </Div>
            <Div
              style={{
                padding: 2,
                margin: 0,
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
              <Div>–û—Å—Ç–∞–ª–æ—Å—å –Ω–∞ –ø—Ä–æ–¥–∞–∂—É: {remainingDC / 100_000}</Div>
              <Div className="iconDC" style={{ marginLeft: 3 }} />
              <Div>({remainingAmountRUB} —Ä—É–±)</Div>
            </Div>
          </Div>
        }
        asideContent={
          <Div id={id} onClick={openExchangeBuy} style={{ color: "green", userSelect: "none" }}>
            –ö—É–ø–∏—Ç—å
          </Div>
        }
        // before={<Icon24FavoriteOutline/>}
        // indicator={rating}
        // indicator={<Icon24FavoriteOutline fill="black"/>}
      >
        <Div style={{ marginLeft: 10 }}>{fullName}</Div>
      </Cell>
    </Div>
  );
};

export { ExchangeBuyItem };
